
import {mapGetters} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import selectCity from '@/components/SelectCity'
import {insuranceInfo} from '../config'
export default {
  name: 'offerDetail',
  head: {
    title: {
      inner: '填写投保信息'
    }
  },
  data () {
    return {
      tips: false,
      loading: false,
      company: {},
      agree: false,
      orderUser: {
        license: '',
        name: '',
        tel: ''
      }
    }
  },
  components: {
    Group,
    XAddress,
    Cell,
    XButton,
    XImg,
    selectCity,
    XInput,
    Popup
  },
  created () {
    // 获取保险公司信息
    // this.company = JSON.parse(this.$localStorage.get('orderCompany'))
    this.getInfo()
    if (this.$route.params.userId === 'null') {
      this.$vux.toast.show({
        type: 'text',
        width: '20em',
        position: 'bottom',
        text: '无法继续操作，请返回登录！',
        time: '1000'
      })
    }
    // if (this.$localStorage.get('logined') !== 'true') {
    //   this.$vux.toast.show({
    //     type: 'text',
    //     width: '20em',
    //     position: 'bottom',
    //     text: '您尚未登录，请前往登录！',
    //     time: '1000'
    //   })
    //   this.$router.push('/login')
    // }
  },
  computed: {
    ...mapGetters({
      offer: 'getOffer',
      InsuranceArea: 'getInsuranceArea',
      checkAuthor: 'checkAuthor'
    })
  },
  methods: {
    toggleTips (agree) {
      this.tips = !this.tips
      if (agree) {
        this.agree = true
      }
    },
    getInfo () {
      this.$http({
        method: 'jsonp',
        url: insuranceInfo,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: {
          companyId: this.$route.params.id
        }
      })
      .then(res => {
        this.company = res.body.data.company
        this.$localStorage.set('orderCompany', JSON.stringify({
          companyId: this.company.id,
          banner: this.company.bigPic,
          companyName: this.company.name
        })) // 存储订单公司信息
      })
    },
    handleSubmit () {
      const reg = /^[\u4e00-\u9fa5]*$/
      console.log(reg.test(this.orderUser.name))
      this.loading = true
      if (this.$route.params.userId === 'null') {
        this.$vux.toast.show({
          type: 'text',
          width: '10em',
          position: 'bottom',
          text: '请登录！',
          time: '1000'
        })
      } else if (!this.InsuranceArea) {
        this.$vux.toast.show({
          type: 'text',
          width: '15em',
          position: 'bottom',
          text: '请填写所在地区！',
          time: '1000'
        })
      } else if (!this.$refs.license.valid) {
        this.$vux.toast.show({
          type: 'text',
          width: '18em',
          position: 'bottom',
          text: '填写车牌号有误！',
          time: '1000'
        })
      } else if (!reg.test(this.orderUser.name)) {
        this.$vux.toast.show({
          type: 'text',
          width: '18em',
          position: 'bottom',
          text: '姓名必须为中文且不能为空！',
          time: '1000'
        })
      } else if (!this.$refs.tel.valid) {
        this.$vux.toast.show({
          type: 'text',
          width: '18em',
          position: 'bottom',
          text: '手机号码填写有误！',
          time: '1000'
        })
      } else if (!this.agree) {
        this.$vux.toast.show({
          type: 'text',
          width: '18em',
          position: 'bottom',
          text: '请阅读并同意免责申请！',
          time: '1000'
        })
      } else {
        // 存储用户订单信息到localstorage
        this.$localStorage.set('orderUser', JSON.stringify({
          insuranceArea: this.InsuranceArea,
          ownerName: this.orderUser.name,
          ownerLicense: this.orderUser.license,
          ownerTel: this.orderUser.tel
        }))
        this.$router.push('/offer/photograph/' + this.$route.params.id + '/' + this.$route.params.userId)
      }
      this.loading = false
    }
  }
}



